import React from 'react'
import { css } from '@emotion/core'
import { Link } from 'Config/routes'
import { withRouter } from "next/router"
import PropTypes from 'prop-types'
import {
  Menu, Icon,
} from 'antd'

const { SubMenu } = Menu

const SideBar = ({ router }) => {
  const { pathname } = router

  return(
    <Menu
      selectedKeys={[pathname]}
      defaultOpenKeys={['sub1']}
      mode="inline"
      theme='light'
      css={css`
        border-right: 0;
        margin-top: 80px;
      `}
    >
      <Menu.Item key="/dashboard">
        <Link route="dashboard">
          <a>
            <Icon type="home" />
            <span>Dashboard</span>
          </a>
        </Link>
      </Menu.Item>
      <Menu.Item key="/pos">
        <Link route="pos">
          <a>
            <Icon type="solution" />
            <span>Point Of Sales</span>
          </a>
        </Link>
      </Menu.Item>
      <Menu.Item key="/listOfEmployee">
        <Icon type="user" />
        <Link route="listOfEmployee">
          <span>Daftar Karyawan</span>
        </Link>
      </Menu.Item>
      {/* <Menu.Item key="4">
        <Icon type="file-protect" />
        <Link route="incomeReport">
          <span>Laporan Pendapatan</span>
        </Link>
      </Menu.Item> */}
      <SubMenu key="sub1" title={<span><Icon type="hdd" />Master Data</span>}>
        <Menu.Item key="/masterData/shiftSetting"><Link route="shiftSetting">Pengaturan Shift</Link></Menu.Item>
        <Menu.Item key="/masterData/position"><Link route="position">Jabatan</Link></Menu.Item>
        <Menu.Item key="/masterData/vehicleType"><Link route="vehicleType">Jenis Kendaraan</Link></Menu.Item>
        <Menu.Item key="/masterData/userType"><Link route="userType">Tipe Pengguna</Link></Menu.Item>
        <Menu.Item key="/masterData/priceType"><Link route="priceType">Tipe Pembayaran</Link></Menu.Item>
        <Menu.Item key="/masterData/howToOrder"><Link route="howToOrder">Cara Pemesanan</Link></Menu.Item>
        <Menu.Item key="/masterData/parkingDuration"><Link route="parkingDuration">Lama Parkir</Link></Menu.Item>
        <Menu.Item key="/masterData/forfeit"><Link route="forfeit">Denda</Link></Menu.Item>
      </SubMenu>
      <Menu.Item key="5">
        <Icon type="mobile" />
        <Link route="">
          <span>Integrasi Palang Pintu</span>
        </Link>
      </Menu.Item>
    </Menu>
  )
}

SideBar.propTypes = {
  router: PropTypes.string
}

export default withRouter(SideBar)